body{background-image:url(images/space_background.jpg);background-size:cover;background-attachment:fixed;height:100%;width:100%}#header{border-bottom:3px solid white;font-family:'Source Code Pro', monospace;display:flex;justify-content:space-evenly;font-size:32px;color:white}#the-real-body{display:flex;justify-content:center}i{color:white}i:hover{color:#d2d2d2;transition:0.5s}a{color:black}.author{min-width:470px}.author-links{min-width:130px}#myCanvas{position:relative;top:100px;border:3px solid white;border-radius:5px;background-image:url(images/game_background.png)}#score-box{border:3px solid white;border-radius:5px;height:43px;width:160px;margin-right:50px;margin-top:40px;color:white;font-family:'Source Code Pro', monospace;padding-top:18px;padding-left:15px}#score-box:hover{border:3px solid black;background-color:white;color:black;transition:0.7s}#instruction-background{position:fixed;top:0;bottom:0;right:0;left:0;background:rgba(0,0,0,0.35);z-index:10;display:none}#final-score-background{position:fixed;top:0;bottom:0;right:0;left:0;background:rgba(0,0,0,0.35);z-index:10;display:none}#instruction-container-top{background-color:white;position:absolute;border-radius:5px;width:350px;top:57%;left:44.3%;transform:translate(-50%, -50%);display:flex;flex-direction:column;justify-content:center;align-items:center;font-family:'Source Code Pro', monospace;padding:20px;border:3px solid #000;color:black}#final-score-container-top{background-color:white;position:absolute;border-radius:5px;width:350px;top:40%;left:45.25%;transform:translate(-50%, -50%);display:flex;flex-direction:column;justify-content:center;align-items:center;font-family:'Source Code Pro', monospace;padding:20px;border:3px solid black;color:black}#instructions{font-family:'Source Code Pro', monospace;color:white;border:3px solid white;padding-left:15px;border-radius:5px;width:130px;margin-top:103px}#instructions:hover{transition:0.7s;background-color:white;color:black;cursor:pointer;border:3px solid #292929}#start-game-page{position:absolute;background-image:url(images/space_corridor.jpg);width:400px;height:600px;top:154px;z-index:5}.start-game-text{margin-top:260px;margin-left:38px}.main-container{display:flex;align-items:center;justify-content:center;color:white;font-family:'Source Code Pro', monospace}#pad{min-width:400px;height:140px;border:3px solid white;border-radius:0 0 5px 5px;position:relative;right:406px;top:560px;z-index:2;background-color:transparent}.rule-pic{display:flex}img{width:200px}.rules{font-size:12px}.rule1{font-size:10px;width:100px;position:relative;top:202px;margin-left:14px}.rule-box{margin-top:18px}#try-again-button{border:solid 3px white;border-radius:5px;color:white;font-family:'Source Code Pro', monospace;background-color:transparent;padding-left:10px;width:152px;margin-top:40px;padding-top:15px;padding-bottom:15px;outline:none;display:none}#try-again-button:hover{cursor:pointer;border:solid 3px black;background-color:white;color:black;transition:0.7s}
